/**
 * 
 * var questionnaire = {
 *     title: String,
 *     deadline: Number,
 *     state: String [],
 *     id: Date.now(), ID
 *     questItemList: Array [questItem] 
 * }
 * var questItem = {
 *     type: radio, checkbox, textarea,
 *     title: '',
 *     name: Date.now()
 *     selections: []
 * }
 *localStorageKEY: questionnaireList, type: Array
 */

import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

const KEY = 'questionnaireList'
let questionnaire01 = {
    "title": "",
    "deadline": 1471622400000,
    "state": "",
    "questItemList": [
        {
            "type": "radio",
            "title": "",
            "selections": [
                "",
                ""
            ],
            "name": 1471528977004
        },
        {
            "type": "checkbox",
            "title": "",
            "selections": [
                "",
                "",
                ""
            ],
            "name": 1471528987079
        },
        {
            "type": "textarea",
            "title": ""
        }
    ],
    "id": 1471528999124
}
let questionnaire02 = {
    "title": "",
    "deadline": 1471622400000,
    "state": "",
    "questItemList": [
        {
            "type": "radio",
            "title": "",
            "selections": [
                "",
                ""
            ],
            "name": 1471528977004
        },
        {
            "type": "checkbox",
            "title": "",
            "selections": [
                "",
                "",
                ""
            ],
            "name": 1471528987079
        },
        {
            "type": "textarea",
            "title": ""
        }
    ],
    "id": 1471528999124
}
let questionnaire03 = {
    "title": "",
    "deadline": 1471622400000,
    "state": "",
    "questItemList": [
        {
            "type": "radio",
            "title": "",
            "selections": [
                "",
                ""
            ],
            "name": 1471528977004
        },
        {
            "type": "checkbox",
            "title": "",
            "selections": [
                "",
                "",
                ""
            ],
            "name": 1471528987079
        },
        {
            "type": "textarea",
            "title": ""
        }
    ],
    "id": 1471528999124
}
if(!localStorage.getItem(KEY)) {
    localStorage.setItem(KEY, JSON.stringify([questionnaire01, questionnaire02, questionnaire03]))
}

const state = {
    questionnaireList: JSON.parse(localStorage.getItem(KEY)),   // ,type: Array
    currentQuestionnaire: null  // , type: {}
}

const mutations = {
    // item
    ADD_QUEST(state, item) {
        state.questionnaireList.push(item)
        state.currentQuestionnaire = item
        updateStorage(state)
    },
    // item
    RM_QUEST(state, item) {
        let index = state.questionnaireList.indexOf(item)
        if(item !== -1) {
            state.questionnaireList.splice(index, 1)
            updateStorage(state)
        } else {
            throw new Error("")
        }
    },
    // 
    UPDATE_QUEST(state, item) {
        let index = state.questionnaireList.indexOf(item)
        if(index !== -1) {
            state.questionnaireList.splice(index, 1, item)
            state.currentQuestionnaire = item
            updateStorage(state)
        } else {
            throw new Error("")
        }
    },
    // 
    SET_QUEST(state, item) {
        if(state.currentQuestionnaire === item) {return}
        state.currentQuestionnaire = item
    }
}

// questionnairelocalStorage
function updateStorage(state) {
    localStorage.setItem(KEY, JSON.stringify(state.questionnaireList))
}

export default new Vuex.Store({
    state, 
    mutations
})


